import React from 'react'
import {Link} from "react-router-dom";

export default class MovieComponent extends React.Component {

  render() {
    return (
        <tr>
          <td><img height='200px' alt={this.props.movie.title}
                   src={this.props.movie.Poster}/></td>
          <td><Link
              to={`/movies/${this.props.movie.imdbID}`}>{this.props.movie.Title}</Link>
          </td>
          <td>{this.props.movie.Year}</td>
        </tr>
    )
  }
}