import React from 'react'
import OMDBserver from "../../Services/OMDBserver";
import CommentComponent from "../CommentComponent";
import MovieTimelineComponent from "./MovieTimelineComponent";
import CommentService from "../../Services/CommentService";
import UserService from "../../Services/UserService";

export default class MovieDetailComponent extends React.Component {

  state = {
    currentUser: {},
    commentText: "Type your comment here",
    movie: {},
    movieId: this.props.match.params.movieId,
    comments: []
  }

  componentDidMount() {
    OMDBserver.searchMovieByID(this.props.match.params.movieId).then(
        res => {
          this.setState({movie: res})
        }
    )
    CommentService.findCommentForMovie(this.props.match.params.movieId)
    .then(res => this.setState({comments: res}))

    UserService.fetchProfile()
    .catch(e => {
      this.props.history.push("/")
    })
    .then(user => UserService.findUserById(user.id)
    .then(res => this.setState({currentUser: res})))
  }

  deleteComment = (cid) =>
      CommentService.deleteComment(cid)
      .then(res => this.setState({
        comments: this.state.comments.filter(c =>
            c.id !== cid)
      }))

  createComment = (mid, newComment) =>
      CommentService.createComment(mid, newComment)
      .then(res =>
          this.setState({
            comments: [...this.state.comments, res]
          }))

  render() {
    return (
        <div>
          <div className={'row'}>
            <div className={'col-3'}>
              <img src={this.state.movie.Poster} alt={this.state.movie.Title}/>
            </div>
            <div className={'col-9'}>
              <h2>{this.state.movie.Title}</h2>
              <p>{this.state.movie.Plot}</p>
              <p>{this.state.movie.Runtime}</p>
            </div>
          </div>
          <h3>Movie Timeline</h3>
          <MovieTimelineComponent movieId={this.state.movieId}/>
          <h3>Comments</h3>
          <ul className={'list-group'}>

            {
              this.state.comments.map(comment => {
                    if (comment.time === -6 && comment.commentFor === -1) {
                      return (<CommentComponent deleteComment={this.deleteComment}
                                                key={comment.id}
                                                movieId={this.state.movieId}
                                                comment={comment}/>)
                    }
                  }
              )
            }

            <li className={'list-group'}>
              <input value={this.state.commentText}
                     onChange={e => this.setState(
                         {commentText: e.target.value})}/>
              <button
                  onClick={() => this.createComment(
                      this.props.match.params.movieId,
                      {
                        text: this.state.commentText,
                        time: -6,
                        commentFor: -1,
                        movie: this.props.match.params.movieId,
                        postedBy: parseInt(this.state.currentUser.id),
                        username: this.state.currentUser.username
                      })}
                  className={'btn btn-success'}>submit
              </button>
            </li>
          </ul>
        </div>
    )
  }
}