const fetchProfile = () =>
    fetch("http://cs4550-20su1-final-java-server.herokuapp.com/api/profile", {
      method: 'POST',
      credentials: "include"
    })
    .then(response => response.json()
    )

const updateProfile = (user) =>
    fetch("http://cs4550-20su1-final-java-server.herokuapp.com/api/profile", {
      body: JSON.stringify(user),
      headers: {
        'content-type': 'application/json'
      },
      method: 'PUT',
      credentials: "include"
    })
    .then(response => response.json())

const logout = () =>
    fetch("http://cs4550-20su1-final-java-server.herokuapp.com/api/logout", {
      method: 'POST',
      credentials: "include"
    })

const login = (un, pw) =>
    fetch("http://cs4550-20su1-final-java-server.herokuapp.com/api/login", {
      body: JSON.stringify({
        username: un,
        password: pw
      }),
      headers: {
        'content-type': 'application/json'
      },
      method: 'POST',
      credentials: "include"
    }).then(response => response.json())

const register = (un, pw,role) =>
    fetch("http://cs4550-20su1-final-java-server.herokuapp.com/api/register", {
      body: JSON.stringify({
        username: un,
        password: pw,
        role:role
      }),
      headers: {
        'content-type': 'application/json'
      },
      method: "POST",
      credentials: "include"
    }).then(response => response.json())

const findUserById = (uid) =>
    fetch(`http://cs4550-20su1-final-java-server.herokuapp.com/api/users/${uid}`)
    .then(res => res.json())

export default {
  fetchProfile,
  updateProfile,
  logout,
  login,
  register,
  findUserById
}