import React from 'react'
import MovieCardComponent from "./movieComponents/MovieCardComponent";

export default class PlaylistComponent extends React.Component {

  state = {
    movies: [{omdbID: 'tt0372784'}, {omdbID: 'tt2975590'},
      {omdbID: 'tt0096895'}, {omdbID: 'tt0103776'}],
    renderedMovie: []
  }



  render() {
    return (
        <div className={"row row-cols-1 row-cols-md-3"}>
          {
            this.state.movies.map(movie => {
              return (
                  <MovieCardComponent key={movie.omdbID} omdbID={movie.omdbID}/>
              )
            })
          }
        </div>
    )
  }
}