import React from 'react'
import {BrowserRouter, Link, Route} from "react-router-dom";
import SearchComponent from "../SearchComponent";
import MovieSearchResultComponent from "./MovieSearchResultComponent";
import HomeComponent from "../HomeComponent"
import ProfileComponent from "../ProfileComponent"
import RegisterComponent from "../RegisterComponent";
import LoginComponent from "../LoginComponent";
import MovieDetailComponent from "./MovieDetailComponent";
import PlaylistComponent from "../PlaylistComponent";
import UserProfileComponent from "../UserProfileComponent";

export default class MovieWebsite extends React.Component {

  render() {
    return (
        <BrowserRouter>
          <div>
            <h1> Movie Zone </h1>
            <Link to='/'>
              HomePage
            </Link>
            <Route exact={true} path='/' exact={true}
                   component={HomeComponent}/>
            <Route exact={true} path={'/profile'} exact={true}
                   component={ProfileComponent}/>
            <Route exact={true} path='/register' exact={true}
                   component={RegisterComponent}/>
            <Route exact={true} path='/login' exact={true}
                   component={LoginComponent}/>
            <Route exact={true} path={['/search', '/search/']}
                   component={SearchComponent}/>
            <Route exact={true} path={'/search/:movieTitle'}
                   component={MovieSearchResultComponent}/>
            <Route exact={true} path={'/movies/:movieId'}
                   component={MovieDetailComponent}/>
            <Route exact={true} path={'/playlist'}
                   component={PlaylistComponent}/>
            <Route exact={true} path={'/profile/:userId'}
                   component={UserProfileComponent}/>
          </div>
        </BrowserRouter>
    )
  }
}
