import React from 'react'
import services from '../../Services/OMDBserver'
import MovieComponent from './MovieComponent'
import {Link} from "react-router-dom";

export default class MovieSearchResultComponent extends React.Component {

  state = {
    title: this.props.match.params.movieTitle,
    movies: [],
  }

  searchMovie = () => {
    services.searchMovieByTitle(this.state.title)
    .then(movies => this.setState({
      movies: movies.Search
    }))
  }

  updateTitleInState = (newTitle) => {
    this.setState(prevState => ({
          title: newTitle
        })
    )
  }

  componentDidMount() {
    this.searchMovie()
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.match.params.movieTitle
        !== this.props.match.params.movieTitle) {
      this.searchMovie()
    }
  }

  render() {
    return (
        <div>
          <h1>Movie Search</h1>
          <label for={"movie"}>Title</label>
          <input id={"movie"}
                 value={this.state.title}
                 onChange={(event) => this.updateTitleInState(
                     event.target.value)}/>
          <Link to={`/search/${this.state.title}`}>
            <button>Search</button>
          </Link>
          <table className={'table table-hover'}>
            <thead>
            <tr>
              <td>Poster</td>
              <td>Title</td>
              <td>Year</td>
            </tr>
            </thead>
            <tbody>
            {
              this.state.movies.map(movie =>
                  <MovieComponent
                      key={movie._id}
                      movie={movie}/>)
            }
            </tbody>
          </table>
        </div>
    )
  }
}