const createComment = (mid, comment) =>
    fetch(`http://cs4550-20su1-final-java-server.herokuapp.com/api/movies/${mid}/comments`, {
      method: 'POST',
      body: JSON.stringify(comment),
      headers: {
        'content-type': 'application/json'
      }
    }).then(res => res.json())

const findCommentForMovie = (mid) =>
    fetch(`http://cs4550-20su1-final-java-server.herokuapp.com/api/movies/${mid}/comments`)
    .then(res => res.json())

const deleteComment = (cid) =>
    fetch(`http://cs4550-20su1-final-java-server.herokuapp.com/api/comments/${cid}`, {
      method: 'DELETE'
    }).then(res => res.json())

export default {
  createComment,
  findCommentForMovie,
  deleteComment
}