import React from 'react'
import UserService from "../Services/UserService";

export default class UserProfileComponent extends React.Component {

  state = {
    user: {},
    currentUser: {}
  }

  componentDidMount() {
    UserService.findUserById(this.props.match.params.userId)
    .then(res => this.setState({
          user: res
        },
        () => {
          UserService.fetchProfile()
          .then(res => this.setState({
            currentUser: res
          }, () => {
            if (this.state.user.id === this.state.currentUser.id) {
              this.props.history.push("/profile")
            }

          }))
        }))

  }

  render() {
    return (
        <div>
          <h3>User Profile for {this.state.user.username}</h3>
          <div className={'container'}>
            {
              this.currentUser.role === 'admin'&&
                  <h1>admin view</h1>
            }
            <ul className={'list-group'}>
              <li className={'list-group-item'}>Username: {this.state.user.username}</li>
              <li className={'list-group-item'}>Phone: {this.state.user.phone}</li>
              <li className={'list-group-item'}>Email: {this.state.user.email}</li>
              <li className={'list-group-item'}>Date of
                Birth{this.state.user.dob}</li>
            </ul>
          </div>
        </div>
    )
  }
}