import React from 'react'
import './MovieTimelineComponent.css'
import CommentComponent from "../CommentComponent";
import CommentService from "../../Services/CommentService";
import OMDBserver from "../../Services/OMDBserver";
import UserService from "../../Services/UserService";

export default class MovieTimelineComponent extends React.Component {
  state = {
    movie: {},
    movieId: this.props.movieId,
    commentText: 'Select a time of the movie and type your comment here',
    time: 0,
    comments: [],
    currentUser: {}
  }

  componentDidMount() {
    OMDBserver.searchMovieByID(this.props.movieId).then(
        res => {
          this.setState({movie: res})
        }
    )
    CommentService.findCommentForMovie(this.state.movieId)
    .then(res => this.setState({comments: res}))

    UserService.fetchProfile()
    .catch(e => {
      this.props.history.push("/")
    })
    .then(user => UserService.findUserById(user.id)
    .then(res => this.setState({currentUser: res})))
  }

  deleteComment = (cid) =>
      CommentService.deleteComment(cid)
      .then(res => this.setState({
        comments: this.state.comments.filter(c =>
            c.id !== cid)
      }))

  createComment = (mid, newComment) =>
      CommentService.createComment(mid, newComment)
      .then(res => this.setState({comments: [...this.state.comments, res]}))

  render() {
    return (
        <div>
          <div className="slidecontainer">
            <input
                onChange={e => this.setState({time: e.target.value})}
                type="range" min="1"
                max={parseInt(this.state.movie.Runtime)}
                value={this.state.time}
                className="slider"
                id="myRange"/>
          </div>
          <h3>Around {this.state.time} mins, we found these comments.</h3>
          <ul>
            {
              this.state.comments.map(
                  comment => {
                    if (parseInt(this.state.time) - 5 < comment.time &&
                        comment.time < parseInt(this.state.time) + 5) {
                      return (
                          <li key={comment.id}
                              className={'list-group-item'}>{comment.time} mins:
                            <CommentComponent
                                deleteComment={this.deleteComment}
                                movieId={this.state.movieId}
                                comments={this.state.comments}
                                comment={comment}/></li>
                      )
                    }
                  }
              )
            }
            <li className={'list-group'}>
              <input value={this.state.commentText}
                     onChange={e => this.setState(
                         {commentText: e.target.value})}/>
              <button
                  onClick={() => this.createComment(this.state.movieId,
                      {
                        text: this.state.commentText,
                        time: this.state.time,
                        commentFor: -1,
                        movie: this.state.movieId,
                        postedBy: parseInt(this.state.currentUser.id),
                        username: this.state.currentUser.username
                      })}
                  className={'btn btn-success'}>submit
              </button>
            </li>
          </ul>
        </div>
    )
  }
}