import React from "react";
import {Link} from "react-router-dom";
import UserService from "../Services/UserService";

export default class HomeComponent extends React.Component {
  state = {
    login: false,
    user: {}
  }

  componentDidMount() {
    UserService.fetchProfile()
    .catch(e => {
      this.setState({login: false})
    })
    .then(actualUser => {
      if (actualUser) {
        UserService.findUserById(actualUser.id).then(res => this.setState({
          user: res,
          login: true
        }))

      }
    })
  }

  render() {
    return (
        <div>
          <h3>Home</h3>
          {this.state.login &&
          <h3>
            Welcome Home {this.state.user.username} Role
            : {this.state.user.role}
          </h3>
          }

          {!this.state.login &&
          <h3>
            To use MovieZone, please login or register a new account
          </h3>
          }

          <div className="list-group">
            <Link className="list-group-item" to='/search'>
              Movie Search
            </Link>
            {this.state.login &&
            <Link className="list-group-item" to='/profile'>
              Profile
            </Link>
            }
            <Link className="list-group-item" to='/register'>
              Register
            </Link>
            {!this.state.login &&
            <Link className="list-group-item" to='/login'>
              Login
            </Link>
            }
            {
              this.state.login &&
              <button className={'btn btn-danger'}
                      onClick={()=>UserService.logout()
                      .then(res => window.location.reload(false))}>sign out</button>
            }
          </div>
        </div>)
  }
}
