import React from 'react'
import CommentService from "../Services/CommentService";
import {Link} from "react-router-dom";
import UserService from "../Services/UserService";

export default class CommentForCommentComponent extends React.Component {

  state = {
    comments: this.props.comments,
    currentUser: {}
  }

  deleteComment = (cid) =>
      CommentService.deleteComment(cid)
      .then(res => this.setState({
        comments: this.state.comments.filter(c =>
            c.id !== cid)
      }))

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.comments !== this.props.comments) {
      this.setState({comments: this.props.comments})
    }

    UserService.fetchProfile()
    .catch(e => {
      this.props.history.push("/")
    })
    .then(user => UserService.findUserById(user.id)
    .then(res => this.setState({currentUser: res})))
  }

  render() {
    return (
        <ul className={'list-group'}>
          {
            this.state.comments.map(comment =>
                <li key={comment.id}
                    className={'list-group-item'}>
                  <Link
                      to={`/profile/${comment.postedBy}`}>{comment.username}</Link>
                  {comment.text}
                  {
                    (this.state.currentUser.id === comment.postedBy
                        || this.state.currentUser.role === 'admin') &&
                    <button className={'btn-sm btn-danger float-right'}
                            onClick={() => this.deleteComment(
                                comment.id)}>delete</button>
                  }
                </li>
            )
          }
        </ul>
    )
  }
}