import React from 'react'
import OMDBserver from "../../Services/OMDBserver";
import {Link} from "react-router-dom";

export default class MovieCardComponent extends React.Component {

  state = {
    movie: {}
  }

  componentDidMount() {
    OMDBserver.searchMovieByID(this.props.omdbID)
    .then(res => {
      this.setState({movie: res})
    })
  }

  render() {
    return (
        <div className="col mb-4">
          <div className="card h-100">
            <img src={this.state.movie.Poster} className="card-img-top"
                 alt={this.state.movie.Title}/>
            <div className="card-body">
              <h5 className="card-title"><Link
                  to={`/movies/${this.state.movie.imdbID}`}>{this.state.movie.Title}</Link>
              </h5>
              <h5 className="card-text">{this.state.movie.Year}</h5>
            </div>
          </div>
        </div>
    )
  }
}
