import React from "react";
import {Link} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.css';
import UserService from "../Services/UserService";

export default class ProfileComponent extends React.Component {

  state = {
    user: {
      username: '',
      password: '',
    },
    updated: false,
  }

  update = () => {
    this.setState({updated: false})
    UserService.updateProfile(this.state.user)
    .then(user => this.setState({
          user: user,
          updated: true,
        }
    ))
  }

  logout = () => {
    UserService.logout()
    .then(response => this.props.history.push("/"))
  }

  componentDidMount() {
    UserService.fetchProfile()
    .catch(e => {
      this.props.history.push("/")
    })
    .then(actualUser => {
      if (actualUser) {
        UserService.findUserById(actualUser.id).then(res => this.setState({
          user: res
        }))

      }
    })
  }

  render() {
    return (
        <div>
          <h3>Profile</h3>
          <Link className="list-group-item" to='/'>
            HomePage
          </Link>
          <div className="container">
            {this.state.updated &&
            <div className="form-group row alertFld wbdv-message">
              <div className="alert alert-success wbdv-message" role="alert">
                Profile saved!
              </div>
            </div>
            }

            <div className="form-group row">

              <label className="col-lg-2 col-sm-6 col-form-label"
                     htmlFor="username">Username</label>
              <div className="col-lg-10 col-sm-6">

                <input id="username"
                       className="form-control wbdv-field wbdv-username"
                       value={this.state.user.username}
                       type="text"
                       placeholder="joe123"
                       title="Use this username to login"
                       readOnly/>
                {
                  this.props.match.params.userId === this.state.user.id &&
                  <h3>{this.state.user.username}</h3>
                }
              </div>
            </div>

            <div className="form-group row">
              <label className="col-lg-2 col-sm-6 col-form-label"
                     htmlFor="password">Password</label>
              <div className="col-lg-10 col-sm-6">

                <input id="password"
                       className="form-control wbdv-field wbdv-password"
                       value={this.state.user.password}
                       onChange={e => this.setState({
                         user: {
                           ...this.state.user,
                           password: e.target.value
                         }
                       })}
                       type="password"/>
              </div>
            </div>


            <div className="form-group row">
              <label className="col-lg-2 col-sm-6 col-form-label"
                     htmlFor="phone">Phone</label>
              <div className="col-lg-10 col-sm-6">

                <input id="phone"
                       className="form-control wbdv-field wbdv-phone"
                       value={this.state.user.phone}
                       onChange={e => this.setState({
                         user: {
                           ...this.state.user,
                           phone: e.target.value
                         }
                       })}
                       type="number"/>

              </div>
            </div>

            <div className="form-group row">
              <label className="col-lg-2 col-sm-6 col-form-label"
                     htmlFor="email">Email</label>
              <div className="col-lg-10 col-sm-6">

                <input id="email"
                       className="form-control wbdv-field wbdv-dob"
                       value={this.state.user.email}
                       onChange={e => this.setState({
                         user: {
                           ...this.state.user,
                           email: e.target.value
                         }
                       })}
                       type="email"/>
              </div>
            </div>


            <div className="form-group row">

              <label className="col-lg-2  col-form-label"
                     htmlFor="dob">Date of birth</label>
              <input id="dob"
                     className="form-control col-lg-10 wbdv-field wbdv-dob"
                     type="date"
                     value={this.state.user.dob}
                     onChange={e => this.setState({
                       user: {
                         ...this.state.user,
                         dob: e.target.value
                       }
                     })}/>
            </div>

            <div className="form-group row">
              <label className="col-lg-2 col-sm-6 col-form-label"
                     htmlFor="role">Role</label>
              <div className="col-lg-10 col-sm-6">
                <select id="role"
                        className="form-control wbdv-field wbdv-dob"
                        value={this.state.user.role}
                        onChange={e => this.setState({
                          user: {
                            ...this.state.user,
                            role: e.target.value
                          }
                        })}>
                  <option value={null}>--</option>
                  <option value="user">User</option>
                  <option value="admin">Admin</option>
                </select>
              </div>
            </div>

            <div className="form-group row">
              <div className="col-lg-2">
                <button
                    onClick={this.update}
                    className="btn btn-success wbdv-button wbdv-update"
                    id="updateBtn">Update
                </button>
                <button
                    className={'btn btn-danger'}
                    onClick={this.logout}>Sign Out
                </button>

              </div>

            </div>
          </div>
        </div>
    )
  }
}

