import React from 'react'
import CommentForCommentComponent from "./CommentForCommentComponent";
import CommentService from "../Services/CommentService";
import {Link} from "react-router-dom";
import UserService from "../Services/UserService";

export default class CommentComponent extends React.Component {

  state = {
    commentText: 'Type your comment here',
    comment: this.props.comment,
    commentOnHere: [],
    makeComment: false,
    movieId: this.props.movieId,
    currentUser: {}
  }

  createComment = (mid, newComment) => {
    console.log(newComment)
    this.setState({makeComment: false})
    CommentService.createComment(mid, newComment)
    .then(res => this.setState(
        {commentOnHere: [...this.state.commentOnHere, res]}))
  }

  componentDidMount() {
    CommentService.findCommentForMovie(this.state.movieId)
    .then(res => {
      this.setState({
        commentOnHere: res.filter(c =>
            c.commentFor === this.state.comment.id
        )
      })
    })

    UserService.fetchProfile()
    .catch(e => {
      this.props.history.push("/")
    })
    .then(user => UserService.findUserById(user.id)
    .then(res => this.setState({currentUser: res})))
  }

  render() {
    return (
        <li className={'list-group-item'}>
          <Link
              to={`/profile/${this.state.comment.postedBy}`}>{this.state.comment.username}</Link>
          <h4>{this.state.comment.text}</h4>
          <button className={'btn btn-sm btn-info'}
                  onClick={() => this.setState({makeComment: true})}>Comment
          </button>

          {
            (this.state.currentUser.id === this.state.comment.postedBy
                || this.state.currentUser.role === 'admin') &&
            <button className={'btn-sm btn-danger'}
                    onClick={() => this.props.deleteComment(
                        this.state.comment.id)}>delete</button>
          }

          <CommentForCommentComponent comments={this.state.commentOnHere}/>
          {
            this.state.makeComment &&
            <div>
              <input value={this.state.commentText}
                     onChange={e => this.setState(
                         {commentText: e.target.value})}/>
              <button
                  onClick={() => this.createComment(this.state.movieId,
                      {
                        text: this.state.commentText,
                        time: -6,
                        commentFor: this.state.comment.id,
                        movie: this.state.movieId,
                        postedBy: parseInt(this.state.currentUser.id),
                        username: this.state.currentUser.username
                      })}
                  className={'btn btn-success'}>submit
              </button>
            </div>
          }
        </li>
    )
  }
}